  padding: 0;
}
.loader:empty {
  position: absolute;
  top: calc(50% - 4em);
  left: calc(50% - 3em);
  width: 6em;
  height: 6em;
  border: 1.1em solid #f26d6463;
  border-left: 1.1em solid #f26d64;
  border-radius: 50%;
  -webkit-animation: load8 1.1s infinite linear;
          animation: load8 1.1s infinite linear;
}

@-webkit-keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

